import React, { useState, useEffect, useRef } from 'react'
import { motion } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import './AboutCards.scss'

import Elipse from '../../../img/elipse/about-elipse.svg'
import CssIcon from '../../../icon/stack-icons/css.svg'
import TypeScriptIcon from '../../../icon/stack-icons/typescript.svg'
import HtmlIcon from '../../../icon/stack-icons/html.svg'
import CshrpIcon from '../../../icon/stack-icons/csharp.svg'
import TailwindIcon from '../../../icon/stack-icons/tailwind.svg'
import ReactIcon from '../../../icon/stack-icons/react.svg'

const About_2 = () => {
	const { t } = useTranslation()
	// State to manage which icon is hovered
	const [hoveredIcon, setHoveredIcon] = useState<string | null>(null)
	const [isVisible, setIsVisible] = useState(false)
	const [animationPlayed, setAnimationPlayed] = useState(false)
	const aboutRef = useRef(null)

	useEffect(() => {
		const observer = new IntersectionObserver(
			([entry]) => {
				// Only set to visible if the animation hasn't been played yet
				if (entry.isIntersecting && !animationPlayed) {
					setIsVisible(true)
					setAnimationPlayed(true) // Prevent future animations
				}
			},
			{
				root: null, // viewport
				threshold: 0.1, // Trigger when 10% of the element is in view
			}
		)

		if (aboutRef.current) {
			observer.observe(aboutRef.current)
		}

		return () => {
			if (aboutRef.current) {
				observer.unobserve(aboutRef.current)
			}
		}
	}, [animationPlayed]) // D

	// Event handlers that accept an icon identifier
	const handleMouseEnter = (iconId: string) => setHoveredIcon(iconId)
	const handleMouseLeave = () => setHoveredIcon(null)

	// Animation variants for the borders
	const getWidthHeightOfPlanet = (width: string, height: string) => ({
		hidden: { width: 0, height: 0 },
		visible: { width, height }, // Use the parameters
	})

	// Animation variants for the images
	const planetAnimation = (initX: string, initY: string, endX: string, endY: string) => ({
		hidden: { opacity: 0, y: parseFloat(initY), x: parseFloat(initX) },
		visible: { opacity: 1, y: parseFloat(endY), x: parseFloat(endX) },
	})

	//Variables
	//Circle Big
	const circle1 = 0.3
	const circle2 = 0.5
	const circle3 = 0.7
	const globalDelay = 0.6
	//Planets
	const planetDelay = 0.7
	const planetDuration = 0.4
	//Orbit PLanets
	const orbitDelay = 1.2
	const orbitDuration = 0.2

	const planetIconData1 = [
		{
			key: 'tailwind',
			src: TailwindIcon,
			alt: 'TailwindIcon',
			icon: 'tailwind',
			orbitWidth: '56px',
			orbitHeight: '56px',
			planetInitX: '100',
			planetInitY: '20',
			planetEndX: '-21',
			planetEndY: '-13',
		},
		{
			key: 'react',
			src: ReactIcon,
			alt: 'ReactIcon',
			icon: 'react',
			orbitWidth: '64px',
			orbitHeight: '64px',
			planetInitX: '-50',
			planetInitY: '40',
			planetEndX: '-13',
			planetEndY: '-16',
		},
	]
	const planetIconData2 = [
		{
			key: 'csharp',
			src: CshrpIcon,
			alt: 'CshrpIcon',
			icon: 'csharp',
			orbitWidth: '98px',
			orbitHeight: '98px',
			planetInitX: '30',
			planetInitY: '100',
			planetEndX: '-20',
			planetEndY: '-25',
		},
	]
	const planetIconData3 = [
		{
			key: 'css',
			src: CssIcon,
			alt: 'cssIcon',
			icon: 'css',
			orbitWidth: '72px',
			orbitHeight: '72px',
			planetInitX: '150',
			planetInitY: '100',
			planetEndX: '-15',
			planetEndY: '-15',
		},
		{
			key: 'typescript',
			src: TypeScriptIcon,
			alt: 'TypeScriptIcon',
			icon: 'typescript',
			orbitWidth: '72px',
			orbitHeight: '72px',
			planetInitX: '-120',
			planetInitY: '200',
			planetEndX: '-15',
			planetEndY: '-15',
		},
		{
			key: 'html',
			src: HtmlIcon,
			alt: 'HtmlIcon',
			icon: 'html',
			orbitWidth: '80px',
			orbitHeight: '80px',
			planetInitX: '-170',
			planetInitY: '100',
			planetEndX: '-15',
			planetEndY: '-15',
		},
	]

	const BigOrbit = (props: { orbitNum: string; orbitSize: string; durationType: any; iconData: any }) => (
		<motion.div
			className={`aboutElipse-border aboutElipse-border-${props.orbitNum}`}
			variants={getWidthHeightOfPlanet(props.orbitSize, props.orbitSize)}
			initial='hidden'
			animate='visible'
			transition={{ duration: props.durationType, delay: globalDelay }}>
			{props.iconData.map(
				(planetIcon: {
					key: React.Key | null | undefined
					src: string
					alt: string
					icon: string
					orbitWidth: string
					orbitHeight: string
					planetInitX: string
					planetInitY: string
					planetEndX: string
					planetEndY: string
				}) => (
					<PlanetIcon
						key={planetIcon.key}
						src={planetIcon.src}
						alt={planetIcon.alt}
						icon={planetIcon.icon}
						orbitWidth={planetIcon.orbitWidth}
						orbitHeight={planetIcon.orbitHeight}
						planetInitX={planetIcon.planetInitX}
						planetInitY={planetIcon.planetInitY}
						planetEndX={planetIcon.planetEndX}
						planetEndY={planetIcon.planetEndY}
					/>
				)
			)}
		</motion.div>
	)


	const PlanetIcon = (props: {
		src: string
		alt: string
		icon: string
		orbitWidth: string
		orbitHeight: string
		planetInitX: string
		planetInitY: string
		planetEndX: string
		planetEndY: string
	}) => (
		<div className=''>
			<motion.div
				className={`iconCircle-${props.icon}`}
				variants={getWidthHeightOfPlanet(props.orbitWidth, props.orbitHeight)}
				initial='hidden'
				animate='visible'
				transition={{ delay: orbitDelay + globalDelay, duration: orbitDuration }}></motion.div>
			<motion.img
				src={props.src}
				alt={props.alt}
				className={`icon icon-${props.icon}`}
				variants={planetAnimation(props.planetInitX, props.planetInitY, props.planetEndX, props.planetEndY)}
				initial='hidden'
				animate='visible'
				transition={{ delay: planetDelay + globalDelay, duration: planetDuration }}
			/>
		</div>
	)

	return (
		<div
			className='aboutCard aboutCard-2'
			ref={aboutRef}>
			<div className=''>
				<p className='aboutCard-2-heading'>{t('about.secondCard.title')}</p>
			</div>
			<div className=''>
				<div className='aboutElipse'>
					<img
						src={Elipse}
						alt=''
						className='aboutElipse-center'
					/>
				</div>
				<BigOrbit
					orbitNum='3'
					orbitSize='558px'
					durationType={circle3}
					iconData={planetIconData3}
				/>
				<BigOrbit
					orbitNum='2'
					orbitSize='429px'
					durationType={circle2}
					iconData={planetIconData2}
				/>
				<BigOrbit
					orbitNum='1'
					orbitSize='300px'
					durationType={circle1}
					iconData={planetIconData1}
				/>
			</div>
		</div>
	)
}

export default About_2
