import { RefObject } from 'react'
import SecondaryButton from '../../../../components/Button/SecondaryButton'
import '../WorkDesktop.scss'
import { motion, useScroll, useTransform } from 'framer-motion'

interface CardInfoProps {
	targetRef?: RefObject<HTMLDivElement>
	title: string
	text: string
	button: string
	startY: number
	endY: number
	hideStart: number
	show: number
	hideEnd: number
}

const CardInfo = ({ targetRef, title, text, button, startY, endY, hideStart, show, hideEnd }: CardInfoProps) => {
	const { scrollYProgress } = useScroll({
		target: targetRef,
	})
	const y = useTransform(scrollYProgress, [startY, endY], ['50%', '-50%'])
	const opacity = useTransform(scrollYProgress, [hideStart, show, hideEnd], [0, 1, 0])

	return (
		<motion.div
			className='cardInfo'
			style={{ y, opacity }}>
			<div className=''>
				<p
					className='workCards-title'
					dangerouslySetInnerHTML={{ __html: title }}
				/>
			</div>
			<div className=''>
				<p className='workCards-text'>{text}</p>
			</div>
			<div className='flex mt-8'>
				<SecondaryButton text={button} />
			</div>
		</motion.div>
	)
}

export default CardInfo
