import { useState } from 'react'
import { useTranslation } from 'react-i18next'

import SaioFull from '../../../icon/logos/saio-full-logo.png'

import './AboutCards.scss'

const About_1 = () => {
	const {t} = useTranslation()
	const [hoveredIcon, setHoveredIcon] = useState<boolean>(false)

	// Event handlers that accept an icon identifier
	const handleMouseEnter = () => setHoveredIcon(true)
	const handleMouseLeave = () => setHoveredIcon(false)

	return (
		<div
			className='aboutCard aboutCard-1'
			onMouseEnter={handleMouseEnter}
			onMouseLeave={handleMouseLeave}>
			<div className='aboutCard-1-heading'>
				<div className=''>
					<img
						src={SaioFull}
						alt=''
						className={`aboutCard-1-logo ${hoveredIcon ? 'aboutCard-1-logo-hover' : ''}`}
					/>
				</div>
				<p className=''>{t('about.firstCard.title')}</p>
			</div>
			<div className=''>
				<p className='aboutCard-1-text'>
					{t('about.firstCard.text')}
				</p>
			</div>
		</div>
	)
}

export default About_1
