import React, { useState, useEffect } from 'react'

import './Header.scss'
import ProfilePic from '../../img/profilePic/profilePic.jpeg'
import Mainbutton from '../../components/Button/MainButton'

import '../../components/Language/i18n'
import { useTranslation } from 'react-i18next'

const Header = () => {
	const {t, i18n} = useTranslation()

	const [scrollPosition, setScrollPosition] = useState(0)

	const handleScroll = () => {
		const position = window.pageYOffset
		setScrollPosition(position)
	}

	useEffect(() => {
		window.addEventListener('scroll', handleScroll)

		return () => {
			window.removeEventListener('scroll', handleScroll)
		}
	}, [])

	// Calculate the movement of the ellipse-light. It moves down by 50px slowly.
	const ellipseLightStyle = {
		transform: `translateY(${Math.min(scrollPosition / 2, 200)}px)`,
	}
	const ellipseLightStyleMobile = {
		transform: `translateY(${Math.min(scrollPosition / 2, 200)}px)`,
	}

	const appliedStyle = window.innerWidth > 1024 ? ellipseLightStyle : ellipseLightStyleMobile

	return (
		<div
			id='Home'
			className='header sectionWide'>
			<div className=''>
				<div className='header-pic'></div>
			</div>
			<div className=''>
				<p className='header-welcome text-gradient'>{t('header.welcomeMain')}</p>
			</div>
			<div className=''>
				<p className='header-info'>
					{t('header.welcomeSub')}
				</p>
			</div>
			<div className='mt-16'>
				<Mainbutton text={`${t('button.pressMe')}`} />
			</div>
			<div className='ellipse'></div>
			<div
				className='ellipse-light'
				style={appliedStyle}></div>
		</div>
	)
}

export default Header
