import { RefObject } from 'react'
import './CardsDesktop.scss'
import { motion, useMotionValue, useScroll, useTransform } from 'framer-motion'

interface CardProps {
	cardImg: string
	cardNumber: number
	targetRef?: RefObject<HTMLDivElement>
	deg: string
	startRotate: number
	endRotate: number
	startX: number
	endX: number
	hide: number
	show: number
}

const CardDesktop = ({
	cardImg,
	cardNumber,
	targetRef,
	deg,
	startRotate,
	endRotate,
	startX,
	endX,
	hide,
	show,
}: CardProps) => {
	//Zrobić aby x przesówało sie po osiągnieciu przez rotate 0deg,
	const { scrollYProgress } = useScroll({
		target: targetRef,
	})

	const x = useTransform(scrollYProgress, [startX, endX], ['1%', '200%'])
	const rotate = useTransform(scrollYProgress, [startRotate, endRotate], [deg, '0deg'])
	const opacity = useTransform(scrollYProgress, [hide, show], [0, 1])

	const template = ({ rotate, x }: { rotate: string; x: string }) => `rotate(${rotate}) translateX(${x})`

	const transform = useMotionValue(template({ rotate: rotate.get(), x: x.get() }))

	rotate.onChange(() => {
		transform.set(template({ rotate: rotate.get(), x: x.get() }))
	})

	x.onChange(() => {
		transform.set(template({ rotate: rotate.get(), x: x.get() }))
	})

	return (
		<motion.div
			style={{ transform }}
			className={`card`}>
			<motion.img
				style={{ opacity }}
				src={cardImg}
				alt=''
				className='card-img'
			/>
			<div className='card-img-cover'></div>
		</motion.div>
	)
}

export default CardDesktop
