import CardMobile from './CardMobile'
import { motion, useScroll, useTransform } from 'framer-motion'
import { useEffect, useRef } from 'react'

import project1 from '../../../img/work/ZZiR_MiniPageMobile.png'
import project2 from '../../../img/work/strach.jpeg'
import project3 from '../../../img/work/wspolpraca.jpeg'

import '../../../components/Language/i18n'
import { useTranslation } from 'react-i18next'

const WorkCardsContainer = ({ targetRef }: any) => {
	const { t } = useTranslation()
	const cardsData = [
		{
			title: t('work.mobile.title1'),
			img: project1,
			description: t('work.mobile.text1'),
			link: 'https://zzirminipage.pl/',
		},
		{
			title: t('work.mobile.title2'),
			img: project2,
			description: t('work.mobile.text2'),
			link: 'https://strach.com/',
		},
		{
			title: t('work.mobile.title3'),
			img: project3,
			description: t('work.mobile.text3'),
			link: 'https://wspolpraca.com/',
		},
	]
	const container = useRef(null)
	const { scrollYProgress } = useScroll({
		target: container,
		offset: ['start start', 'end end'],
	})

	// useEffect(() => {
	// 	scrollYProgress.on('change', e => console.log(scrollYProgress.get()))
	// }, [])

	return (
		<div
			ref={container}
			className=''>
			{cardsData.map((card, index) => {
				const targetScale = 1 - (cardsData.length - index) * 0.05
				return (
					<CardMobile
						key={index}
						i={index}
						img={card.img}
						title={card.title}
						description={card.description}
						link={card.link}
						progress={scrollYProgress}
						range={[index * 0.33, 1]}
						targetScale={targetScale}
					/>
				)
			})}
		</div>
	)
}

export default WorkCardsContainer
